  //border-radius: 0 12px 0 0;
}

.role-list-content{

  position: relative;
  background-color: rgb(255, 255, 255);
  height: calc(100% - 404px);
}

.role-list-content2{

  position: relative;
  background-color: rgb(255, 255, 255);
  height: calc(100% - 62px);
}

.head-item{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.content-ltr{
  border-right: 1px solid var(ion-color-light);
}

.head-themes{
  float: left;
  display: flex;
}
.head-themes div:first-child{
  font-size: 10px;
  background: var(--ion-color-secondary);
  padding: 6px 12px;
  border-radius: 17px;
  margin-right: 6px;
  font-weight: 700;
  color: #48494E;
  font-family: 'SFProText-bold';
}

.head-themes div:nth-child(2){
  font-size: 20px;
  //font-weight: 700;
  color: var(--ion-color-medium);
  transform: translateY(-4px);
}

.head-themes div:nth-child(3){
  font-size: 14px;
  /* font-weight: 600; */
  color: var(--ion-color-medium);
  padding: 6px;
}

.theme-img{
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
  transition: background-image 1s ease-in-out;
  background-position: center;
  background-size: cover;
  //animation: pinned-img-animation 1s ease-in-out;
}

.theme-pinned-box{
  background: #F5F7F9;
  border-radius: 12px;
  padding: 12px;
  margin: 8px;
  height: 96px;
  overflow: scroll;
}

.theme-roles-box{
  //background: #F5F7F9;
  border-radius: 12px;
  //padding: 12px;
  overflow: scroll;
  height: 100%;
  background: var(--ion-color-tertiary);
  margin: 0 20px 12px;
  padding: 12px 0;
}

.theme-pinned-box div:first-child{
  font-weight: 700;
  font-size: 14px
}

.theme-pinned-box div:nth-child(2){
  font-size: 14px;
  padding: 6px 0;
  line-height: 1.2;
}

.footer-icon{
  width: 24px;
  height: 24px;
}

.footer-btn{
  --border-radius: 30px;
  font-size: 14px;
}

.theme-icon{
  width: 20px;
  height: 20px;
}

.msg-head-avatar{
  padding: 0 0 0 12px;
}

.head-pin-title{
  font-weight: 700;
  font-size: 16px;
  max-width: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header-ios ion-toolbar:last-of-type{
  --border-width: 0 !important;
}

.not-connect{
  position: absolute;
  top: 0;
  z-index: 1000000;
  text-align: center;
  width: 100%;
  background: var(--ion-color-warning);
  color: #fff;
  padding: 2px;
  font-weight: 700;
}


@-webkit-keyframes anim {
  0% {
    opacity: 0;
  }
  36.36% {
    opacity: 0;
  }
  63.63% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes anim {
  0% {
    opacity: 0;
  }
  36.36% {
    opacity: 0;
  }
  63.63% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
//ion-icon { pointer-events: inherit }
ion-icon:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
}